<template>
  <div class="asset-table-action-3">
    <div class="wrapper-title">{{ $t('assets.slots.action3.text1')}}</div>
    <ul class="slot-wrapper">
      <li class="left">
        <el-form 
         :model="actionForm"
         :rules="rules" 
         ref="actionForm" 
         class="action-form">
          <div class="label">{{ $t('assets.slots.action3.text2')}}</div>
          <el-form-item prop="number" class="relative-input">
            <el-input 
              type="number" 
              :placeholder="$t('assets.slots.action3.text3')" 
              v-model="actionForm.number" 
              autocomplete="off"></el-input>
            <el-button 
              type="text" 
              class="all-btn"
              @click="handleAll">{{ $t('assets.slots.action2.text6') }}</el-button>
          </el-form-item>
          <el-form-item>
            <el-button 
              class="submit" 
              type="primary"
              @click="handleDialog">{{ $t('assets.slots.action3.text5')}}</el-button>
          </el-form-item>
        </el-form>
      </li>
      <li class="right">
        <div class="title">{{ $t('assets.slots.tips.text1') }}</div>
        <ul class="list">
          <li class="text">{{ $t('assets.slots.action3.text6') }}</li>
        </ul>
      </li>
    </ul>
    <el-dialog
      class="security-dialog"
      :visible.sync="dialogVisible"
      width="500px">
      <span slot="title">{{ $t('assets.slots.tips.text9')}}</span>
      <el-form 
        :model="actionForm"
        :rules="rules" 
        ref="dialogForm" 
        class="dialog-form"
        label-width="140px">
        <el-form-item 
          :label="$t('assets.slots.tips.text2')" 
          prop="pwdtrade">
          <el-input 
            type="password" 
            :placeholder="$t('assets.slots.tips.text3')" 
            v-model="actionForm.pwdtrade" 
            autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item 
          :label="$t('assets.slots.tips.text10')" 
          prop="sms_verify" 
          class="relative-input">
          <el-input 
            type="text" 
            :placeholder="$t('assets.slots.tips.text11')" 
            v-model="actionForm.sms_verify" 
            autocomplete="off"></el-input>
            <count-down-button 
              class="absolute-button"
              :requestType="'transfer'"></count-down-button>
        </el-form-item>
        <el-form-item label-width="100px">
          <el-button 
            class="submit-button" 
            type="primary"
            @click="handleSubmit">{{ $t('assets.slots.tips.text4')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import md5 from 'js-md5'
  import { platformTransfer } from '@/api/assets/index'
  import CountDownButton from './CountDownButton'
  export default {
    name: 'asset-table-action-3',
    components: {
      CountDownButton
    },
    props: {
      id: {
        type: Number
      },
      type: {
        type: String,
        default() {
          return 'ctc'
        }
      },
      currency: {
        type: String,
        default() {
          return ''
        }
      },
      number: {
        type: String
      }
    },
    data() {
      const validateNumber = (rule, value, callback) => {
        if (!value || Number(value) < 0 || Number(value) === 0) {
          callback(new Error(this.$t('assets.validate.text9')))
        } else {
          callback()
        }
      }
      const validateTransPass = (rule, value, callback) => {
        if (!value || value.length !== 6 || parseInt(value) + '' !== value) {
          callback(new Error(this.$t('assets.validate.text7')))
        } else {
          callback()
        }
      }
      const validateSixNum = (rule, value, callback) => {
        if (!value || value.length !== 6 || parseInt(value) + '' !== value) {
          callback(new Error(this.$t('assets.validate.text8')))
        } else {
          callback()
        }
      }
      return {
        actionForm: {
          transfer_type: this.type === 'ctc' ? 1 : 0,
          currency_id: this.id,
          number: '',
          pwdtrade: '',
          sms_verify: ''
        },
        dialogVisible: false,
        rules: {
          number: [
            { required: true, validator: validateNumber, trigger: 'blur' }
          ],
          pwdtrade: [
            { required: true, validator: validateTransPass, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, validator: validateSixNum, trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleDialog() {
        this.$refs.actionForm.validate(valid => {
          if (valid) {
            this.dialogVisible = true
          } else {
            console.log('error popup!!')
            return false
          }
        })
      },
      handleAll() {
        this.actionForm.number = this.number
      },
      handleSubmit() {
        this.$refs.dialogForm.validate(valid => {
          if (valid) {
            const submitData = {
              transfer_type: this.actionForm.transfer_type,
              currency_id: this.actionForm.currency_id,
              number: this.actionForm.number,
              pwdtrade: md5(this.actionForm.pwdtrade),
              sms_verify: this.actionForm.sms_verify
            }
            platformTransfer(submitData).then(res => {
              this.dialogVisible = false
              this.$notify({
                title: this.$t('assets.slots.tips.text6'),
                type: 'success',
                showClose: false,
                duration: 3000
              })
              this.actionForm = {
                transfer_type: this.type === 'ctc' ? 1 : 0,
                currency_id: this.id,
                number: '',
                pwdtrade: '',
                sms_verify: ''
              }
              this.$emit('refresh', this.type)
            }).catch(() => {
  
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .asset-table-action-3 {
    .wrapper-title{
      font-weight: bold;
      color: #3f4041;
      margin-bottom: 20px;
      font-size: 13px;
    }
    .slot-wrapper{
      font-size: 0px;
      .left{
        display: inline-block;
        vertical-align: top;
        width: 40%;
        font-size: 13px;
        line-height: 1;
        .action-form{
          width: 75%;
          .label{
            color: #7b7e7f;
            margin: 0 0 10px;
          }
          /deep/.el-form-item{
            margin-bottom: 18px;
          }
          .submit{
            margin-top: 10px;
            width: 100%;
          }
        }
      }
      .right{
        display: inline-block;
        vertical-align: top;
        width: 60%;
        font-size: 13px;
        line-height: 1;
        .title{
          color: #7b7e7f;
          margin-bottom: 16px;
        }
        .text{
          color: #3f4041;
          margin-bottom: 10px;
          line-height: 1.2;
          list-style-type: disc;
        }
        .list{
          margin-left: 16px;
        }
      }
    }
    .security-dialog{
      .submit-button{
        width: 100%;
      }
    }
    .relative-input{
      position: relative;
      .all-btn{
        position: absolute;
        top: 0px;
        right: 10px;
      }
      .absolute-button{
        position: absolute;
        top: 0px;
        right: 10px;
      }
    }
  }
</style>



