<template>
  <div id="deep-echart" style="width: 100%;height:360px;"></div>
</template>
 
<script>
import echarts from 'echarts'

export default {
  props: ['data'],
  data: function() {
    return {
      chart: null,
      candlestick: {
        color: ['#37c769', '#fe5c5c'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
          // formatter: function(params) {
          //   var res = this.$t('tradeDetail.chart.part1') + params[0].data[0] + '<br/>  ' + this.$t('tradeDetail.chart.part2') + params[0].data[1]
          //   return res
          // }
        },
        grid: {
          left: '3%',
          right: '6%',
          bottom: '4%',
          top: '10%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'value',
            name: this.$t('tradeDetail.chart.part3'),
            nameTextStyle: {
              color: '#61688a'
            },
            boundaryGap: false,
            axisLine: { lineStyle: { color: '#8392A5' }},
            splitLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#61688a'
              }
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: this.$t('tradeDetail.chart.part4'),
            nameTextStyle: {
              color: '#61688a'
            },
            axisLine: { lineStyle: { color: '#8392A5' }},
            splitLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#61688a'
              }
            }
          }
        ],
        series: [
          {
            name: '',
            smooth: false,
            itemStyle: {
              normal: {
                areaStyle: {
                  type: 'default',
                  color: '#37c769'
                }
              }
            },
            type: 'line',
            areaStyle: { normal: {}}
          },
          {
            name: '',
            smooth: false,
            type: 'line',
            itemStyle: {
              normal: {
                areaStyle: {
                  type: 'default',
                  color: '#fe5c5c'
                }
              }
            },
            areaStyle: { normal: {}}
          }
        ]
      }
    }
  },
  mounted() {

  },
  // watch:{
  //   data: function() {
  //     // console.log(this.data)
  //     this.renderEchart()
  //   }
  // },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initEchart() {
      this.chart = echarts.init(document.getElementById('deep-echart'))
      this.chart.setOption(this.candlestick)
    },
    renderEchart() {
      const option = {
        series: [
          {
            data: this.data.buy_ress
          },
          {
            data: this.data.sell_ress
          }
        ]
      }

      // 使用刚指定的配置项和数据显示图表。
      this.chart.setOption(option)
    }
  }
}
</script>

<style lang="less" scoped>    
</style>



