<template>
  <div class="trade">
    <div class="container">
      <div class="title">{{ $t('translateLang.index.text16') }}</div>
      <p class="title-tips">{{ $t('translateLang.index.text17') }}</p>

      <div class="clearfix trade-area">
        <div class="trade-area-tap trade-coin-tap pull-left">
          <div v-for="(item, index) in tradeList" :key="item.trade_area_cate_id">
            <p v-if="index === 0" class="active currency" @click="handleAreaClick(item.trade_area_cate_id, item.trade_area_list, $event)">
              {{ item.area_cate_name }}
            </p>
            <p v-else class="currency" @click="handleAreaClick(item.trade_area_cate_id, item.trade_area_list, $event)">
              {{ item.area_cate_name }} 
            </p>
          </div> 
        </div>

      <div class="search pull-right">
        <el-input v-model="searchContent" :placeholder="$t('translateLang.index.text18')"><i slot="prefix" class="el-input__icon el-icon-search"></i></el-input>
      </div>

     </div>
     <div>
        <div class="trade-coin-tap trade-area-coin-tap">
        <div v-for="(item, index) in tradeAreaList" :key="item.trade_area_id">
          <p v-if="index === 0" class="active currency" @click="handleClick(item.trade_area_id,  $event)">
            {{ item.currency_mark }}
          </p>
          <!-- <p v-else-if="index === tradeAreaList.length - 1" class="currency border-r"  @click="handleClick(item.trade_area_id,  $event)">
            {{ item.currency_mark }}
          </p> -->
          <p v-else class="currency" @click="handleClick(item.trade_area_id,  $event)">
            {{ item.currency_mark }}
          </p>
        </div>
      </div>

     </div>
    

    <trade-table :id="trade_area_id" :typeId="typeId"  :searchContent="searchContent"></trade-table>
    </div>
  </div>
</template>

<script>
  import tradeTable from './tradeTable'
  import { getToken } from '@/utils/auth'
  import { getAllTradeAreaCateList } from '@/api/index'
  export default {
    components: { tradeTable },
    data() {
      return {
        tradeList: [],
        trade_area_cate_id: 0,
        typeId: 0,
        trade_area_id: 0,
        tradeAreaList: [],
        searchContent: ''
      }
    },
    created() {
      this.initTrade()
    },
    methods: {
      initTrade() {
        const sendData = {}
        if (window.localStorage.getItem('link_userInfo') && getToken()) {
          let link_userInfo = window.localStorage.getItem('link_userInfo')
          link_userInfo = JSON.parse(link_userInfo)
          sendData.user_id = link_userInfo.uid
        }
        getAllTradeAreaCateList(sendData).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.tradeList = [...res.data]
          console.log(this.tradeList)
  
          this.typeId = res.data[0].trade_area_cate_id
          this.tradeAreaList = res.data[0].trade_area_list
          this.trade_area_id = res.data[0].trade_area_list[0].trade_area_id
        }).catch(() => {
        })
      },
      handleAreaClick(id, trade_area_list, event) {
        const pNode = document.querySelectorAll('.trade-area-tap p')
        pNode.forEach(function(node) {
          node.classList.remove('active')
        })
        event.target.classList.add('active')
        this.tradeAreaList = trade_area_list
        if (trade_area_list.length !== 0) {
          this.trade_area_id = trade_area_list[0].trade_area_id
        }
        this.typeId = id
      },
      handleClick(id, event) {
        const pNode = document.querySelectorAll('.trade-area-coin-tap p')
        pNode.forEach(function(node) {
          node.classList.remove('active')
        })
        event.target.classList.add('active')
        this.trade_area_id = id
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .trade {
    background: #eff4fb;
    border-bottom: none;
    padding: 40px 0 100px;
    .trade-area{
      background: white;
      border-bottom: 1px solid #e4e8ec;
    }
    .trade-area-coin-tap{
      height: 55px !important;
      .currency{
        line-height: 55px !important;
      }
    }
    .trade-coin-tap{
      background: white;
      text-align: left;
      color: #606266;
      display: flex;
      padding-left: 17px;
      >div{
        display: inline-block;
        p.currency{
          line-height: 70px;
          padding: 0 13px;
          display: inline-block;
          cursor: pointer;
          font-size: 16px;
          
          &.active{
            color: #1587e7;
          }
        }
      }
    }
    .title{
      text-align: center;
      line-height: 70px;
      font-weight: bold;
      color: #2e3544;
      font-size: 30px;
    }
    .title-tips{
      text-align: center;
      margin-bottom: 70px !important;
      font-size: 18px;
      color: #5d667a;
    }
    .search{
      width: 180px;
      margin-top: 15px;
      display: inline-block;
      margin-right: 30px;
    }
  }
</style>



