<template>
  <div class="coin-dialog container">
    <el-dialog
      :title="$t('tradeDetail.coin')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="1150">
      <div class="order-list">
        <el-tabs v-model="activeID" @tab-click="handleClick" class="tabs">
          <el-tab-pane :label="item.trade_area_name" :name="String(item.id)" v-for="item in tradeArealist" :key="item.id">
            <coin-table :id="item.id"></coin-table>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import coinTable from './coinListDialogTable'
  import { getAllTradeArea } from '@/api/index'
  export default {
    props: ['dialogVisible'],
    components: { coinTable },
    data() {
      return {
        activeID: '1',
        tradeArealist: [],
        trade_area_id: 0
      }
    },
    created() {
      this.initTrade()
      this.trade_area_id = this.$route.query.trade_area_id
      this.activeID = this.$route.query.trade_area_id.toString()
    },
    methods: {
      handleClick(tab, event) {
        // console.log(tab, event);
      },
      handleCloseDialog() {
        this.$emit('closeDialog', 'dialogVisible')
      },
      initTrade() {
        getAllTradeArea().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.tradeArealist = res.data
          this.tradeArealist.forEach(element => {
            if (element.id === this.$route.query.trade_area_id) {
              this.$store.commit('setTradeName', element.currency_mark)
            }
          })
        }).catch(() => {
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .coin-dialog {
    .el-message-box{
      width: 500px;
    }
    .el-tabs__nav-wrap::after{
      height: 1px !important;
    }
   /deep/ .el-dialog__header{
      background: #f1eff4;
    }
    /deep/ .el-dialog__body{
      padding: 0;
      padding-bottom: 40px;
    }
  }
</style>
<style lang="less">
  .coin-dialog {
    .el-tabs__item{
      display: inline-block;
      width: 160px;
      height: 75px;
      line-height: 75px;
      text-align: center;
      font-size: 16px;
      cursor: pointer;
    }
    .el-tabs__active-bar{
      display: none;
    }
  }
</style>



