<template>
  <div class="t-canvas">
    <canvas class="trend" :id="'line' + id" width="200" height="40"></canvas>
  </div>
</template>

<script>
  export default {
    props: ['id', 'trend'],
    data() {
      return {
      }
    },
    mounted() {
      this.drawLine('line' + this.id, this.trend)
    },
    methods: {
      drawLine(canvasId, dataArr) {
        var canvas = document.getElementById(canvasId)
        var ctx = canvas.getContext('2d')
        ctx.clearRect(0, 0, 200, 40)
        var max = Math.max.apply(null, dataArr)
        var min = Math.min.apply(null, dataArr)
        // if the currency value is not changed, draw a horizon line
        if (max === min) {
          ctx.beginPath()
          ctx.moveTo(0, 20)
          ctx.lineTo(200, 20)
          ctx.lineWidth = 1
          ctx.strokeStyle = '#37d06a'
          ctx.closePath()
          ctx.stroke()
        } else {
          var nums = []
          for (var i = 0; i < dataArr.length; i++) {
            nums.push(dataArr[i] * 40 / max)
          }

          for (i = 0; i < nums.length - 1; i++) {
            // gaps of points
            var gap = 200 / nums.length

            var numsY = 40 - nums[i]
            var numsX = i * gap

            var numsNY = 40 - nums[i + 1]
            var numsNX = (i + 1) * gap
            ctx.beginPath()
            ctx.moveTo(numsX, numsY)
            ctx.lineTo(numsNX, numsNY)
            ctx.lineWidth = 1
            ctx.strokeStyle = '#37d06a'
            ctx.closePath()
            ctx.stroke()
          }
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
</style>



