<template>
  <div class="trade-table container">
   <el-table
      :data="tableData"
      style="width: 100%"
      height="640"
      @row-click="handeleToTradeDetail1"
      :default-sort = "{prop: 'currency_mark', order: 'descending'}"
      >
      <el-table-column
      :label="$t('index.part15')"
        width="180">
        <template slot-scope="scope">
          <div class="currency-name"><img :src="scope.row.currency_logo" alt="">{{ scope.row.currency_mark }}<span class="b5b">/{{ scope.row.currency_trade_mark }}</span></div>
          <!-- <div class="currency-name"><img src="static/images/china.png" alt="">{{ scope.row.currency_mark }}</div> -->
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('index.part16')"
        width="180">
        <template slot-scope="scope">
          <div v-if="$i18n.locale == 'zn'">{{ parseFloat(scope.row.last_price).toFixed(5) }}  ≈ ¥ {{  scope.row.exchange_cny_price }}</div>
          <div v-if="$i18n.locale == 'zh-hk'">{{ parseFloat(scope.row.last_price).toFixed(5) }}  ≈ HK$ {{  scope.row.exchange_hkd_price }}</div>
          <div v-if="$i18n.locale == 'en'">{{ parseFloat(scope.row.last_price).toFixed(5) }}  ≈ $ {{  scope.row.exchange_usd_price }}</div>
        </template>
      </el-table-column>
      <el-table-column
        prop="trade_count_num"
        :label="$t('index.part17')"
        >
      </el-table-column>
      <el-table-column
      :label="$t('index.part19')">
        <template slot-scope="scope">
          <div v-if="parseFloat(scope.row.trade_up_down) >= 0" class="c-green">+{{ scope.row.trade_up_down }}</div>
          <div v-if="parseFloat(scope.row.trade_up_down) < 0" class="c-red">{{ scope.row.trade_up_down }}</div>
        </template>d
      </el-table-column>
      <el-table-column
        prop="trade_count_money"
        :label="$t('index.part20')"
        >
        <template slot-scope="scope">
          <div>
            <canv :id="scope.row.currency_id" :trend="scope.row.trade_price_trend"></canv>  
          </div>
        </template>
      </el-table-column>
      <!-- <el-table-column
        label="">
        <template slot-scope="scope">
          <div class="t-center">
            <el-button class="violet-border-btn trade-btn" @click="handeleToTradeDetail(scope.row.trade_area_id, scope.row.currency_id, scope.row.currency_mark, scope.row.currency_name)">{{ $t('finace.part9') }}</el-button>
          </div>
        </template>
      </el-table-column> -->
    </el-table>
  </div>
</template>

<script>
  import canv from './canvas'
  import { getToken } from '@/utils/auth'
  export default {
    props: {
      id: Number,
      typeId: Number,
      searchContent: String
    },
    components: { canv },
    data() {
      return {
        ws: null,
        tradeDataGetInterval: null,
        tableData: [],
        wsData: []
      }
    },
    created() {
      this.initWebsoketLink()
    },
    beforeRouteLeave(to, from, next) {
      clearInterval(this.tradeDataGetInterval)
      this.ws.onclose = function() {
      }
      next()
    },
    watch: {
      id: function(val) {
        console.log(val)
        clearInterval(this.tradeDataGetInterval)
        this.initWebsoketLink()
      },
      typeId: function(val) {
        console.log(val)
        clearInterval(this.tradeDataGetInterval)
        this.initWebsoketLink()
      },
      searchContent: function(val) {
        this.tableData = this.searchContentMatch(this.wsData)
      }
    },
    beforeDestroy() {
      if (this.tradeDataGetInterval) { // 如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.tradeDataGetInterval) // 关闭
      }
      this.ws.onclose = function() {
      }
    },
    methods: {
      handeleToTradeDetail(trade_area_id, currency_id, currency_mark, currency_name) {
        this.$router.push({ name: 'tradeDetail', query: { 'trade_area_id': trade_area_id, 'currency_id': currency_id, 'currency_mark': currency_mark, 'currency_name': currency_name }})
      },
      formatter(row, column) {
        return row.address
      },
      searchContentMatch(data) {
        if (this.searchContent === '') {
          return data
        }
  
        console.log(this.searchContent)
        const newData = []
        data.forEach(element => {
          const reg = new RegExp(this.searchContent, 'i')
          if (reg.test(element.currency_mark)) {
            newData.push(element)
          }
        })
        console.log(newData)
        return newData
      },
      handeleToTradeDetail1(row) {
        this.$router.push({ name: 'tradeDetail', query: { 'trade_area_id': row.trade_area_id, 'currency_id': row.currency_id, 'currency_mark': row.currency_mark, 'currency_name': row.currency_name }})
      },
      initWebsoketLink() {
        this.ws = new WebSocket(this.$store.state.tradeUrl)
        const _this = this
        this.ws.onopen = function() {
          console.log('交易表格数据连接成功')
  
          const sendData = {
            'trade_area_id': _this.id,
            'trade_area_cate_id': _this.typeId,
            'request_type': 'area'
          }
          if (window.localStorage.getItem('link_userInfo') && getToken()) {
            let link_userInfo = window.localStorage.getItem('link_userInfo')
            link_userInfo = JSON.parse(link_userInfo)
            sendData.user_id = link_userInfo.uid
          }
          console.log(sendData)
          if (_this.ws.readyState === 1) {
            _this.ws.send(JSON.stringify(sendData))

            // 添加定时器，每隔一段时间向后台发送一次数据
            _this.tradeDataGetInterval = window.setInterval(function() {
              if (_this.ws.readyState === 1) {
                _this.ws.send(JSON.stringify(sendData))
              }
              console.log('数据已发送')
            }, 8000)
          }
        }

        this.ws.onmessage = function(e) {
          var obj = JSON.parse(e.data)
          console.log(obj)
  
          if (obj.data.length !== 0 && Number(obj.data[0].trade_area_id) === _this.id) {
            _this.wsData = [...obj.data]
            _this.tableData = _this.searchContentMatch(obj.data)
          }
          if (_this.id === 0) {
            _this.tableData = []
          }
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .trade-table {
    background: white;
    font-size: 14px;
    .trade-btn{
      padding: 6px 11px;
    }
    /deep/ .el-table td{
      font-size: 14px;
      padding-left: 15px;
      cursor: pointer;
    }
    .currency-name{
      img{
        width: 20px;
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    .b5b{
      color: #b5bbca;
    }
  }
</style>

<style lang="less">
.trade-table {
  .el-table th{
    background: #fafbff !important;
    padding-left: 15px;
  }
  .tbody tr:nth-of-type(odd) {
    background: #f2f8fe;
  }
}
</style>


