<template>
  <div class="trade-table-area1">
    <el-table
      :data="tableData[id.toString()+typeId.toString()]"
      style="width: 100%"
      :default-sort="{prop: 'currency_mark', order: 'descending'}"
      @row-click="handeleToTradeDetail1"
      :row-class-name="handleRowActive"
    >
      <el-table-column 
        prop="currency_mark"
        :label="$t('translateLang.tradeDetail.text58')"
        width="80px" 
        sortable>
        <template slot-scope="scope">
          <!-- <div class="currency-name">{{ scope.row.currency_mark }}</div> -->
          <div class="currency-name" v-if="scope.row.is_collect == 1">
            <img
              src="/static/images/trade_detail/active_star.png"
              alt
              @click.stop="handleOptionalCollect(scope.row.currency_id, scope.row.trade_area_id, 0)"
            >
            {{ scope.row.currency_mark }}
          </div>
          <div class="currency-name" v-if="scope.row.is_collect == 0">
            <img
              src="/static/images/trade_detail/normal_star.png"
              alt
              @click.stop="handleOptionalCollect(scope.row.currency_id, scope.row.trade_area_id, 1)"
            >
            {{ scope.row.currency_mark }}
          </div>
        </template>
      </el-table-column>
      <el-table-column 
        prop="last_price"
        :label="$t('translateLang.tradeDetail.text59')" 
        width="134px" 
        sortable>
        <template slot-scope="scope">
          <div
            v-if="$i18n.locale == 'zn'"
          >{{ parseFloat(scope.row.last_price).toFixed(5) }} <div class="c-616 about-price">≈ ¥ {{ scope.row.exchange_cny_price }}</div></div>
          <div
            v-if="$i18n.locale == 'zh-hk'"
          >{{ parseFloat(scope.row.last_price).toFixed(5) }}<div class="c-616 about-price"> ≈ HK$ {{ scope.row.exchange_hkd_price }}</div></div>
          <div
            v-if="$i18n.locale == 'en'"
          >{{ parseFloat(scope.row.last_price).toFixed(5) }} <div class="c-616 about-price"> ≈ $ {{ scope.row.exchange_usd_price }}</div></div>
        </template>
      </el-table-column>
      <el-table-column 
        prop="trade_up_down"
        sortable 
        :label="$t('translateLang.tradeDetail.text55')">
        <template slot-scope="scope">
          <div
            v-if="parseFloat(scope.row.trade_up_down) >= 0"
            class="c-green"
          >+{{ scope.row.trade_up_down }}</div>
          <div
            v-if="parseFloat(scope.row.trade_up_down) < 0"
            class="c-red"
          >{{ scope.row.trade_up_down }}</div>
        </template>d
      </el-table-column>
    </el-table>
  </div>
  <!-- trade_area_id=6&currency_id=9&trade_currency_mark=CAIT&trade_currency_name=CAIT -->
</template>

<script>
import { getToken } from '@/utils/auth'
import { optionalCollect } from '@/api/trade'
export default {
  props: {
    id: Number,
    typeId: Number,
    searchContent: String
  },
  data() {
    return {
      rowActive: '',
      ws: null,
      tradeDataGetInterval: null,
      tableData: {},
      wsData: [],
      indexActive: -1
    }
  },
  created() {
    this.initWebsoketLink()
    this.rowActive = this.$route.query.currency_id
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.tradeDataGetInterval)
    this.ws.close()
    next()
  },
  watch: {
    id: function(val) {
      console.log(val)
      clearInterval(this.tradeDataGetInterval)
      this.initWebsoketLink()
    },
    typeId: function(val) {
      console.log(val)
      clearInterval(this.tradeDataGetInterval)
      this.initWebsoketLink()
    },
    searchContent: function(val) {
      this.$set(
        this.tableData,
        this.id.toString() + this.typeId.toString(),
        this.searchContentMatch(this.wsData)
      )
    },

    $route(to, from) {
      // 监听路由是否变化
      // window.location.reload()
    }
  },
  beforeDestroy() {
    if (this.tradeDataGetInterval) {
      // 如果定时器还在运行 或者直接关闭，不用判断
      clearInterval(this.tradeDataGetInterval) // 关闭
    }
    this.ws.onclose = function() {}
  },
  methods: {
    handleRowActive({ row, rowIndex }) {
      row.index = rowIndex
      if (rowIndex === this.indexActive) {
        return 'active'
      }
    },
    handeleToTradeDetail1(row, event, column) {
      this.indexActive = row.index
      this.$router.push({
        name: 'tradeDetail',
        query: {
          trade_area_id: row.trade_area_id,
          currency_id: row.currency_id,
          currency_mark: row.currency_mark,
          currency_name: row.currency_name
        }
      })
    },
    formatter(row, column) {
      return row.address
    },
    searchContentMatch(data) {
      if (this.searchContent === '') {
        data.forEach(element => {
        // if(element.currency_mark === this.$route.query.)
        })
        return data
      }

      console.log(this.searchContent)
      const newData = []
      data.forEach(element => {
        const reg = new RegExp(this.searchContent, 'i')
        if (reg.test(element.currency_mark)) {
          newData.push(element)
        }
      })
      console.log(newData)
      return newData
    },
    handleSendData() {
      const sendData = {
        trade_area_id: this.id,
        trade_area_cate_id: this.typeId,
        request_type: 'area'
      }
      if (window.localStorage.getItem('link_userInfo') && getToken()) {
        let link_userInfo = window.localStorage.getItem('link_userInfo')
        link_userInfo = JSON.parse(link_userInfo)
        sendData.user_id = link_userInfo.uid
      }

      this.ws.send(JSON.stringify(sendData))
    },
    initWebsoketLink() {
      this.ws = new WebSocket(this.$store.state.tradeUrl)
      const _this = this
      this.ws.onopen = function() {
        console.log('交易表格数据连接成功')

        const sendData = {
          trade_area_id: _this.id,
          trade_area_cate_id: _this.typeId,
          request_type: 'area'
        }
        if (window.localStorage.getItem('link_userInfo') && getToken()) {
          let link_userInfo = window.localStorage.getItem('link_userInfo')
          link_userInfo = JSON.parse(link_userInfo)
          sendData.user_id = link_userInfo.uid
        }
        if (_this.ws.readyState === 1) {
          _this.ws.send(JSON.stringify(sendData))

          // 添加定时器，每隔一段时间向后台发送一次数据
          _this.tradeDataGetInterval = window.setInterval(function() {
            if (_this.ws.readyState === 1) {
              _this.ws.send(JSON.stringify(sendData))
            }
            console.log(sendData)
            console.log('--数据已发送--')
          }, 8000)
        }
      }

      this.ws.onmessage = e => {
        var obj = JSON.parse(e.data)
        console.log('-----交易区数据----')
        console.log(obj)

        if (
          obj.data.length !== 0 &&
          Number(obj.data[0].trade_area_id) === _this.id
        ) {
          // if (obj.data.length !== 0) {
          _this.wsData = [...obj.data]

          this.$set(
            _this.tableData,
            this.id.toString() + this.typeId.toString(),
            _this.searchContentMatch(obj.data)
          )
        }
        if (_this.id === 0) {
          _this.tableData = []
        }
        if (obj.data.length === 0) {
          _this.tableData = []
        }
      }
    },
    handleOptionalCollect(currency_id, trade_area_id, type) {
      const data = {
        trade_area_id: trade_area_id,
        currency_id: currency_id,
        type: type
      }
      optionalCollect(data)
        .then(res => {
          if (res.status === 0) {
            this.$message.error(this.$t('translateLang.tradeDetail.text73'))
            return false
          }
          if (res.status !== 1000) {
            // this.$message.error('收藏失败！')
            return false
          }
          if (type === 0) {
            this.$message.success(this.$t('translateLang.tradeDetail.text74'))
          } else {
            this.$message.success(this.$t('translateLang.tradeDetail.text75'))
          }
          // this.$emit('initTrade')
          // setTimeout(() => {
          //   this.handleSendData()
          // }, 1000)
          this.handleSendData()
        })
        .catch(() => {})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.trade-table-area1 {
  min-height: 544px;
  background: #191b2b;
  font-size: 12px;
  .trade-btn {
    padding: 6px 11px;
  }
}
</style>

<style lang="less">
.trade-table-area1 {
  table {
    border-spacing: none;
  }
  .el-table th > .cell {
    color: #61688a;
  }
  .el-table td div {
    color: #b0b8db;
    line-height: 22px;
    vertical-align: middle;
  }
  .el-table td,
  .el-table th {
    padding: 3px 0;
    background: #191b2b !important;
    cursor: pointer;
  }
  .el-table tr.active td,
  .el-table tr:hover td{
    background: rgba(25,27,43,.9) !important;
  }
  .about-price{
    line-height: 16px !important;
  }
  .currency-name {
    img {
      margin-right: 5px;
      vertical-align: middle;
      display: inline-block;
      cursor: pointer;
    }
  }
  .trade-area-coin-tap {
    background: #131625 !important;
  }
}
</style>


