<template>
  <div class="k-echart-container">
    <div id="k-echart" style="width: 100%;height:360px;"></div>
    <div class="k-echart-tooltips">{{ this.$t('tradeDetail.chart.part5')  }}=<span class="tooltip-open"></span>{{ this.$t('tradeDetail.chart.part6')  }}=<span class="tooltip-high"></span>{{ this.$t('tradeDetail.chart.part7')  }}=<span class="tooltip-low"></span>{{ this.$t('tradeDetail.chart.part8')  }}=<span class="tooltip-close"></span></div>
  </div>
</template>
 
<style>
.echarts {
  width: 100%;
  height: 500px;
}
</style>
 
<script>
import echarts from 'echarts'

export default {
  props: ['data'],
  data: function() {
    return {
      chart: null,
      openData: '',
      highData: '',
      lowData: '',
      closeData: '',
      candlestick: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          formatter: (params) => {
            document.querySelector('.tooltip-open').innerHTML = params[0].data[1]
            document.querySelector('.tooltip-high').innerHTML = params[0].data[3]
            document.querySelector('.tooltip-low').innerHTML = params[0].data[4]
            document.querySelector('.tooltip-close').innerHTML = params[0].data[2]
            const res = 'K Line<br/>' + this.$t('add.time') + params[0].axisValue + '<br/>' + this.$t('add.open') + params[0].data[1] + '<br/>' + this.$t('add.close') + params[0].data[2] + '<br/>' + this.$t('add.hight') + params[0].data[3] + '<br/>' + this.$t('add.low') + params[0].data[4] + '<br/>'
            return res
          }
        },
        legend: {
          show: false,
          data: ['K Line']
        },
        grid: {
          left: '50px',
          right: '50px',
          bottom: '50px',
          top: '50px'
        },
        xAxis: {
          type: 'category',
          name: this.$t('tradeDetail.chart.part14'),
          nameTextStyle: {
            color: '#61688a'
          },
          splitNumber: 13,
          scale: true,
          axisLine: {
            onZero: false,
            lineStyle: {
              color: '#444655'
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#444655'
            }
          },
          axisLabel: {
            textStyle: {
              color: '#61688a'
            }
          },
          min: 'dataMin',
          max: 'dataMax'
        },
        yAxis: {
          scale: true,
          name: this.$t('tradeDetail.chart.part15'),
          nameTextStyle: {
            color: '#61688a'
          },
          splitNumber: 6,
          splitLine: {
            show: true,
            lineStyle: {
              color: '#444655'
            }
          },
          axisLabel: {
            textStyle: {
              color: '#61688a'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#bfbdbd'
            }
          }
        },
        dataZoom: [
          {
            type: 'inside'
            // start: 80,
            // end: 100
          }
        ],
        series: [
          {
            name: 'K Line',
            type: 'candlestick',
            itemStyle: {
              normal: {
                color: '#ec0000',
                color0: '#00da3c',
                borderColor: '#8A0000',
                borderColor0: '#008F28'
              }
            }
          }
        ]
      }
    }
  },
  mounted() {
    this.initEchart()
  },
  watch: {
    data: function() {
    // console.log(this.data)
      this.renderEchart()
    }
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initEchart() {
      this.chart = echarts.init(document.getElementById('k-echart'))
      // this.chart.showLoading({
      //   text : "",
      //   textStyle: { color: '#444' },
      //   effectOption: {backgroundColor: 'rgba(125, 88, 209, 0.2)'}
      // })
      this.chart.setOption(this.candlestick)
    },
    renderEchart() {
      if (this.data.values.length > 0) {
        // this.chart.hideLoading()
      }
      console.log('刷新K线图')
      const option = {
        xAxis: {
          data: this.data.categoryData
        },
        series: [
          {
            data: this.data.values
          }
        ]
      }

      // 使用刚指定的配置项和数据显示图表。
      this.chart.setOption(option)
    }
  }
}
</script>

<style lang="less" scoped>
  .k-echart-container{
    position: relative;
    color: #3e434c;
    .k-echart-tooltips{
      position: absolute;
      top: 10px;
      left: 100px;
    }
    .k-echart-tooltips span{
      margin-right: 8px;
    }
    .k-echart-tooltips .tooltip-open{
      color: #646eec;
    }
    .k-echart-tooltips .tooltip-high{
      color: #45cb74;
    }
    .k-echart-tooltips .tooltip-low{
      color: #ff8383;
    }
    .k-echart-tooltips .tooltip-close{
      color: #dc5ffb;
    }
    
  }
    
</style>



