<template>
  <div class="asset-table-action-1" v-loading="loading">
    <ul class="slot-wrapper">
      <li class="left">
        <div class="title">{{ $t('assets.slots.action1.text1')}}</div>
        <div class="label">{{ $t('assets.slots.action1.text2')}}</div>
        <div class="address">
          <span class="text">{{ address ? address : $t('assets.slots.action1.text8') }}</span>
          <el-button
            v-if="address" 
            type="text" 
            class="action" 
            @click="handleCopy(address, $event)">{{ $t('assets.slots.action1.text9')}}</el-button>
          <el-popover
            v-if="address"
            class="pop-over"
            placement="bottom"
            trigger="hover">
            <div class="qrcode-address-text">{{ $t('assets.slots.action1.text7')}}</div>
            <qrcode
            :text="address"
            :width="150"
            :height="150"></qrcode>
            <span class="icon" slot="reference"></span>
          </el-popover>
        </div>
      </li>
      <li class="right">
        <div class="title">{{ $t('assets.slots.tips.text1') }}</div>
        <ul class="list">
          <li class="text">{{ action1Text3 }}</li>
          <li class="text">{{ action1Text4 }}</li>
          <li class="text">{{ $t('assets.slots.action1.text5') }} {{ leastNum }} {{ action1Text6 }}</li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  import { getCtcAddress, getOtcAddress } from '@/api/assets/index'
  import Qrcode from '@/components/Qrcode'
  import clipboard from '@/utils/clipboard'
  export default {
    name: 'asset-table-action-1',
    components: {
      Qrcode
    },
    props: {
      id: {
        type: Number
      },
      type: {
        type: String,
        default() {
          return 'ctc'
        }
      },
      currency: {
        type: String,
        default() {
          return ''
        }
      },
      leastNum: {
        type: Number,
        default() {
          return 0
        }
      }
    },
    data() {
      return {
        address: '',
        loading: false
      }
    },
    mounted() {
      this.getAddress()
    },
    computed: {
      action1Text3() {
        let result = this.$t(`assets.slots.action1.text3`)
        result = result.replace(/%currency%/g, this.currency)
        return result
      },
      action1Text4() {
        let result = this.$t(`assets.slots.action1.text4`)
        result = result.replace(/%currency%/g, this.currency)
        return result
      },
      action1Text6() {
        let result = this.$t(`assets.slots.action1.text6`)
        result = result.replace(/%currency%/g, this.currency)
        return result
      }
    },
    methods: {
      handleCopy(text, event) {
        clipboard(text, event).then(() => {
          this.$message({
            message: this.$t('assets.slots.tips.text7'),
            duration: 1000,
            type: 'success'
          })
        }).catch(() => {
          this.$message({
            message: this.$t('assets.slots.tips.text8'),
            duration: 1000,
            type: 'warining'
          })
        })
      },
      getAddress() {
        this.loading = true
        if (this.type === 'ctc') {
          getCtcAddress({ currency_id: this.id }).then(res => {
            console.log('res', res)
            this.loading = false
            this.address = res.data.wallet_address
          }).catch(() => {
  
          })
        } else {
          getOtcAddress({ currency_id: this.id }).then(res => {
            console.log('res-otc', res)
            this.loading = false
            this.address = res.data.wallet_address
          }).catch(() => {
  
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .asset-table-action-1 {
    .slot-wrapper{
      font-size: 0px;
      .left{
        display: inline-block;
        vertical-align: top;
        width: 40%;
        font-size: 13px;
        line-height: 1;
        .title{
          font-weight: bold;
          color: #3f4041;
        }
        .label{
          color: #7b7e7f;
          margin: 10px 0 20px;
        }
        .address{
          .text{
            display: inline-block;
            vertical-align: middle;
            font-weight: bold;
            color: #3f4041;
          }
          .action{
            display: inline-block;
            vertical-align: middle;
            padding: 0px;
            margin: 0 10px 0 5px;
          }
          .pop-over{
            display: inline-block;
            vertical-align: middle;
            .icon{
              display: inline-block;
              vertical-align: middle;
              cursor: pointer;
              width: 17px;
              height: 17px;
              background: url('/static/images/assets/code-mini.png') no-repeat center center;
            }
          }
        }
      }
      .right{
        display: inline-block;
        vertical-align: top;
        width: 60%;
        font-size: 13px;
        line-height: 1;
        .title{
          color: #7b7e7f;
          margin-bottom: 16px;
        }
        .text{
          color: #3f4041;
          margin-bottom: 10px;
          line-height: 1.2;
          list-style-type: disc;
        }
        .list{
          margin-left: 16px;
        }
      }
    }
  }
</style>



