<template>
  <div class="trade-head">
    <div class="chart-title">
      <h3><span class="trade-name">{{ tradeInfo.currency_mark }}/{{ tradeInfo.trade_currency_mark }}  {{ tradeInfo.last_price }}</span>  ≈  
      <span v-if="$i18n.locale == 'zn'" class="c-616"> {{ (tradeInfo.last_price * tradeInfo.exchange_cny_price).toFixed(2) }} CNY</span>
      <span v-if="$i18n.locale == 'zh-hk'" class="c-616"> HK${{ (tradeInfo.last_price * tradeInfo.exchange_hkd_price).toFixed(2) }}</span>
      <span v-if="$i18n.locale == 'en'" class="c-616"> ${{ (tradeInfo.last_price * tradeInfo.exchange_usd_price).toFixed(2) }}</span>
      </h3>
      <h3 v-if="tradeInfo.trade_up_down >= 0">{{ $t('translateLang.tradeDetail.text55') }} <span class="c-green" > +{{ tradeInfo.trade_up_down }}%</span></h3>
      <h3 v-if="tradeInfo.trade_up_down < 0">{{ $t('translateLang.tradeDetail.text55') }} <span class="c-red" > {{ tradeInfo.trade_up_down }}%</span></h3>
      <h3>{{ $t('translateLang.tradeDetail.text56') }} <span> {{ tradeInfo.big_price }}</span></h3>
      <h3>{{ $t('translateLang.tradeDetail.text57') }} <span> {{ tradeInfo.small_price }}</span></h3>
      <h3>{{ $t('translateLang.tradeDetail.text25') }}  <span> {{ tradeInfo.trade_count_num }} {{ tradeInfo.trade_currency_mark }}</span></h3>      
      </div>
  </div>
</template>

<script>
  // import { getBannerList } from '@/api/index'
  import coinListDialog from './coinListDialog'
  export default {
    props: {
      tradeInfo: Object
    },
    components: { coinListDialog },
    data() {
      return {
        dialogVisible: false
      }
    },
    methods: {
      closeDialog(str) {
        this[str] = false
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .trade-head {
    background: #25283c;
    color: #61688a;
  }
  .c-616{
    color: #61688a !important;
  }
    .chart-title{
      width: 100%;
      background: #131625;
      h3{
        display: inline-block;
        line-height: 45px;
        font-weight: normal;
        font-size: 14px;
        margin-left: 16px;
        span{
          color: #b0b8db;
        }
      }
      .trade-name{
        font-size: 18px;
        color: #b0b8db;
        // margin-left: 20px;
      }
    }
    @media screen and (max-width: 1559px) {
      .chart-title{
        h3{
          margin-left: 1px;
          font-size: 12px;
          span{
            font-size: 12px;
          }
        }
        .trade-name{
          font-size: 15px;
        }
      }
    }
</style>



