<template>
  <div class="asset-table-action-2" v-loading="loading">
    <div class="wrapper-title">{{ $t('assets.slots.action2.text1')}}</div>
    <ul class="slot-wrapper">
      <li class="left">
        <el-form 
         :model="actionForm"
         :rules="rules" 
         ref="actionForm" 
         class="action-form">
          <div class="label">{{ $t('assets.slots.action2.text2')}}</div>
          <el-form-item prop="turn_out_address">
            <el-input 
              type="text" 
              :placeholder="$t('assets.slots.action2.text3')" 
              v-model="actionForm.turn_out_address" 
              autocomplete="off"></el-input>
          </el-form-item>
          <div class="label">{{ $t('assets.slots.action2.text4')}}</div>
          <el-form-item prop="turn_out_num" class="relative-input">
            <el-input 
              type="number" 
              :placeholder="$t('assets.slots.action2.text5')" 
              v-model.trim="actionForm.turn_out_num" 
              autocomplete="off"></el-input>
            <el-button 
              type="text" 
              class="all-btn"
              @click="handleAll">{{ $t('assets.slots.action2.text6') }}</el-button>
          </el-form-item>
          <div class="label">{{ $t('assets.slots.action2.text7')}}</div>
          <el-form-item>
            <el-input 
              type="textarea" 
              v-model="actionForm.remarks"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button 
              class="submit" 
              type="primary"
              @click="handleDialog">{{ $t('assets.slots.action2.text9')}}</el-button>
          </el-form-item>
        </el-form>
      </li>
      <li class="right">
        <div class="title">{{ $t('assets.slots.tips.text1') }}</div>
        <ul class="list">
          <li class="text">{{ $t('assets.slots.action2.text10') }} {{ minNum }} {{ currency }}</li>
          <!-- <li class="text">{{ action2Text11 }}{{ action2Text12 }}</li> -->
          <li class="text">{{ action2Text13 }}</li>
          <li class="text">{{ $t('assets.slots.action2.text14') }}</li>
          <li class="text">{{ $t('assets.slots.action2.text15') }}</li>
        </ul>
      </li>
    </ul>
    <el-dialog
      class="security-dialog"
      :visible.sync="dialogVisible"
      width="500px">
      <span slot="title">{{ $t('assets.slots.tips.text9')}}</span>
      <el-form 
        :model="actionForm"
        :rules="rules" 
        ref="dialogForm" 
        class="dialog-form"
        label-width="140px">
        <el-form-item 
          :label="$t('assets.slots.tips.text2')" 
          prop="trade_password">
          <el-input 
            type="password" 
            :placeholder="$t('assets.slots.tips.text3')" 
            v-model="actionForm.trade_password" 
            autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item 
          :label="$t('assets.slots.tips.text10')"
          prop="sms_verify" 
          class="relative-input">
          <el-input 
            type="text" 
            :placeholder="$t('assets.slots.tips.text11')" 
            v-model="actionForm.sms_verify" 
            autocomplete="off"></el-input>
            <count-down-button 
              class="absolute-button"
              :requestType="'changeout'"></count-down-button>
        </el-form-item>
        <el-form-item>
          <el-button 
            class="submit-button" 
            type="primary"
            @click="handleSubmit">{{ $t('assets.slots.tips.text4')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import md5 from 'js-md5'
  import { memberAssetOutTo, getCurrencyMinTibiNum } from '@/api/assets/index'
  import CountDownButton from './CountDownButton'
  export default {
    name: 'asset-table-action-2',
    components: {
      CountDownButton
    },
    props: {
      id: {
        type: Number
      },
      type: {
        type: String,
        default() {
          return 'ctc'
        }
      },
      currency: {
        type: String,
        default() {
          return ''
        }
      },
      number: {
        type: String
      }
    },
    computed: {
      action2Text11() {
        let result = this.$t(`assets.slots.action2.text11`)
        result = result.replace(/%currency%/g, this.currency)
        return result
      },
      action2Text12() {
        let result = this.$t(`assets.slots.action2.text12`)
        result = result.replace(/%currency%/g, this.currency)
        return result
      },
      action2Text13() {
        let result = this.$t(`assets.slots.action2.text13`)
        result = result.replace(/%currency%/g, this.currency)
        return result
      }
    },
    data() {
      const validateAddress = (rule, value, callback) => {
        if (!value) {
          callback(new Error(this.$t('assets.validate.text1')))
        } else {
          if (value.length > 43 || value.length < 33) { // 提币地址长度需在33-43位之间
            // 错误提示
            callback(new Error(this.$t('assets.validate.text2')))
          } else {
            callback()
          }
        }
      }
      const validateNumber = (rule, value, callback) => {
        if (!value || Number(value) < 0 || Number(value) === 0) {
          callback(new Error(this.$t('assets.validate.text3')))
        } else {
          callback()
        }
      }
      const validateTransPass = (rule, value, callback) => {
        if (!value || value.length !== 6 || parseInt(value) + '' !== value) {
          callback(new Error(this.$t('assets.validate.text7')))
        } else {
          callback()
        }
      }
      const validateSixNum = (rule, value, callback) => {
        if (!value || value.length !== 6 || parseInt(value) + '' !== value) {
          callback(new Error(this.$t('assets.validate.text8')))
        } else {
          callback()
        }
      }
      return {
        actionForm: {
          currency_id: this.id,
          turn_out_address: '',
          turn_out_num: '',
          trade_password: '',
          sms_verify: '',
          remarks: null
        },
        dialogVisible: false,
        loading: false,
        minNum: null,
        rules: {
          turn_out_address: [
            { required: true, validator: validateAddress, trigger: 'blur' }
          ],
          turn_out_num: [
            { required: true, validator: validateNumber, trigger: 'blur' }
          ],
          trade_password: [
            { required: true, validator: validateTransPass, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, validator: validateSixNum, trigger: 'blur' }
          ]
        }
      }
    },
    mounted() {
      this.getMinNum()
    },
    methods: {
      getMinNum() {
        const data = {
          currency_id: this.id,
          currency_type: this.type === 'ctc' ? 1 : 2
        }
        this.loading = true
        getCurrencyMinTibiNum(data).then(res => {
          this.loading = false
          this.minNum = res.data.min_num
        }).catch(() => {
  
        })
      },
      handleDialog() {
        this.$emit('refresh', 'ctc')
        this.$refs.actionForm.validate(valid => {
          if (valid) {
            this.dialogVisible = true
          } else {
            console.log('error popup!!')
            return false
          }
        })
      },
      handleAll() {
        this.actionForm.turn_out_num = this.number
      },
      handleSubmit() {
        this.$refs.dialogForm.validate(valid => {
          if (valid) {
            const submitData = {
              currency_id: this.actionForm.currency_id,
              turn_out_address: this.actionForm.turn_out_address,
              turn_out_num: this.actionForm.turn_out_num,
              trade_password: md5(this.actionForm.trade_password),
              sms_verify: this.actionForm.sms_verify,
              remarks: this.actionForm.remarks
            }
            memberAssetOutTo(submitData).then(res => {
              this.dialogVisible = false
              this.$notify({
                title: this.$t('assets.slots.tips.text5'),
                type: 'success',
                showClose: false,
                duration: 3000
              })
              this.actionForm = {
                currency_id: this.id,
                turn_out_address: '',
                turn_out_num: '',
                trade_password: '',
                sms_verify: '',
                remarks: null
              }
              this.$emit('refresh', 'ctc')
            }).catch(() => {
  
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .asset-table-action-2 {
    .wrapper-title{
      font-weight: bold;
      color: #3f4041;
      margin-bottom: 20px;
      font-size: 13px;
    }
    .slot-wrapper{
      font-size: 0px;
      .left{
        display: inline-block;
        vertical-align: top;
        width: 40%;
        font-size: 13px;
        line-height: 1;
        .action-form{
          width: 75%;
          .label{
            color: #7b7e7f;
            margin: 0 0 10px;
          }
          /deep/.el-form-item{
            margin-bottom: 18px;
          }
          .submit{
            margin-top: 10px;
            width: 100%;
          }
        }
      }
      .right{
        display: inline-block;
        vertical-align: top;
        width: 60%;
        font-size: 13px;
        line-height: 1;
        .title{
          color: #7b7e7f;
          margin-bottom: 16px;
        }
        .text{
          color: #3f4041;
          margin-bottom: 10px;
          line-height: 1.2;
          list-style-type: disc;
        }
        .list{
          margin-left: 16px;
        }
      }
    }
    .security-dialog{
      .submit-button{
        width: 100%;
      }
    }
    .relative-input{
      position: relative;
      .all-btn{
        position: absolute;
        top: 0px;
        right: 10px;
      }
      .absolute-button{
        position: absolute;
        top: 0px;
        right: 10px;
      }
    }
  }
</style>



