<template>
  <el-dialog
      :title="$t('translateLang.tradeDetail.text43')"
      :visible.sync="dialogVisible"
      :before-close="handleCloseDialog"
      :close-on-click-modal=false
      :close-on-press-escape=false
      width="490px" class="wait-pass-dialog">
      <el-form :model="ruleForm" :rules="ruleFormRules" ref="ruleForm" label-width="80px" class="form-group">
        <paybox @payPwd="(val)=>ruleForm.trade_password=val" :dialogVisible="dialogVisible" class="pay-box"></paybox>
        <!-- <el-form-item :label="$t('translateLang.tradeDetail.text44')" prop="trade_password" >
          <el-input v-model.trim="ruleForm.trade_password" type="password"></el-input>
        </el-form-item> -->
        <!-- <el-form-item :label="$t('form.googlePwd')" prop="pass_double" v-if="is_set_double_certification">
          <el-input v-model.trim="ruleForm.pass_double" :placeholder="$t('form.googlePwdPlaceholder')"></el-input>
        </el-form-item> -->
        <div class="btn-group">
          <el-button type="primary"  @click="formSubmit('ruleForm')">{{ $t('translateLang.tradeDetail.text45') }}</el-button>
        </div>
      </el-form>
    </el-dialog>
</template>

<script>
  import { checkToken } from '@/api/user'
  import Paybox from '@/components/Paybox'
  import md5 from 'js-md5'
  export default {
    props: ['dialogVisible'],
    components: { Paybox },
    data() {
      return {
        ruleForm: {
          trade_password: '',
          pass_double: ''
        },
        is_set_double_certification: false,
        ruleFormRules: {
          trade_password: [
            { required: true, message: this.$t('errorTips.noTradePwd'), trigger: 'blur' }
          ]
          // pass_double: [
          //   { required: true, message: this.$t('errorTips.noGoogleCode'), trigger: 'blur' },
          // ]
        }
      }
    },
    created() {
      this.getUserInfo()
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'dialogVisible')
      },
      getUserInfo() {
        checkToken().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          if (res.data.is_set_double_certification === 1) {
            this.is_set_double_certification = true
          } else {
            this.ruleForm.pass_double = this.$t('translateLang.tradeDetail.text45')
            this.is_set_double_certification = false
          }
          console.log(res.data)
        }).catch(() => {
        })
      },
      formSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { }
            data.order_id = this.orderId
            data.trade_password = md5(this.ruleForm.trade_password)
            if (this.is_set_double_certification) {
              data.pass_double = this.ruleForm.pass_double
            }
            this.$emit('submitData', this.ruleForm.trade_password)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .wait-pass-dialog{
    .el-form-item {
      margin-bottom: 22px;
    }
    .btn-group{
      text-align: center;
    }
    /deep/ .el-input__inner,
    .person .el-button--primary,
    .el-button {
      width: 290px;
      
    }
    /deep/ .el-button {
      margin-bottom: 20px;
    }
    .el-form-item__label{
      paddding-left: 20px;
    }
    /deep/ .el-button--primary{
      // margin-left: 20px;
    }
    form{
      margin-top: 10px;
    }
     /deep/.el-input .el-input__inner{
       background: white;
       border-color: #e4e7ed;
       color: #606266;
     }
     /deep/.el-input__inner {
      -webkit-appearance: none;
      background-color: #fff;
      background-image: none;
      border-radius: 4px;
      border: 1px solid #dcdfe6;
      box-sizing: border-box;
      color: #606266;
      display: inline-block;
      font-size: inherit;
      height: 40px;
      line-height: 40px;
      outline: none;
      padding: 0 15px;
      transition: border-color .2s cubic-bezier(.645,.045,.355,1);
      width: 100%;
    }
    /deep/ .el-form-item__error{
      left: 0;
    }
    /deep/ .el-dialog__body{
      padding: 0 20px;
    }
    .pay-box{
      margin: 10px 0 20px 80px;
    }
  }
</style>


