<template>
  <div class="coin-dialog-table">
    <ul class="select-coin-content1 clearfix">
      <li  v-for="item in currencylist" :key="item.id">
        <div class="active" @click="getCoinDetail(item.id)"><img :src="item.currency_logo" alt="">{{ item.currency_mark }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
  import { getTradeCurrencys } from '@/api/trade'
  export default {
    props: ['id'],
    data() {
      return {
        currencylist: []
      }
    },
    mounted() {
      this.initCurrency()
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      '$route': function() {
        window.location.reload()
      }
    },
    methods: {
      initCurrency() {
        const data = {
          'currency_trade_id': this.id
        }

        getTradeCurrencys(data).then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.currencylist = [...res.data]
          for (var j = 0; j < (4 - (res.data.length % 4)); j++) {
            this.currencylist.push({})
          }
        }).catch(() => {
        })
      },
      getCoinDetail(id) {
        const tradeData = this.$store.state.tradeDetail
        console.log(tradeData)
        window.location.href = window.location.href.split('?')[0] + '?trade_area_id=' + this.id + '&currency_id=' + id + '&currency_mark=' + tradeData.currency_mark + '&currency_name=' + tradeData.currency_name
        // window.location.reload()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .coin-dialog-table {
    ul li {
    float: left;
    border-bottom: 1px solid #eeeeee;
    width: 25%;
    font-size: 16px;
    padding: 20px;
    box-sizing: border-box;
    cursor: pointer;
    height: 81px;
    img {
      width: 40px;
      margin-right: 15px;
      vertical-align: middle;
      // margin-top: 20px;
    }
    span{
      color: #999999;
      margin-left: 8px;
    }
  }
  /deep/ .el-tabs__header{
    margin: 0;
  }
  /deep/ .el-tabs__active-bar{
    display: none !important;
  }
  // /deep/ .el-tabs__item{
  //   padding: 20px;
  // }
  // /deep/.el-tabs__active-bar{
  //   display: none;
  // }
  // /deep/ .el-tabs__nav-wrap::after{
  //   height: 1px;
  //   background-color: #eeeeee;
  // }
  }
</style>
<style  lang="less">
  .coin-dialog-table{
    .el-tabs__header{
    margin: 0 !important;
  }
   .el-tabs__active-bar{
    display: none !important;
  }
   .el-tabs__item{
    padding: 20px !important;
  }
  .el-tabs__active-bar{
    display: none !important;
  }
  .el-tabs__nav-wrap::after{
    height: 1px !important;
    background-color: #eeeeee !important;
  }
  }
</style>


