<template>
  <div class="count-down-button">
    <el-button
      class="code-btn"
      :disabled="sendTime !== 60 "
      @click="getCode"
      type="text">
        {{ sendTime >= 60 ? $t('assets.slots.tips.text12') : sendTime }}{{ sendTime >= 60 ? '' : $t('assets.slots.tips.text13') }}
    </el-button>
  </div>
</template>

<script>
  import { sendRequestSms } from '@/api/assets/index'
  let sendBtnTimer
  export default {
    name: 'count-down-button',
    components: {

    },
    props: {
      requestType: {
        type: String,
        default() {
          return 'changeout'
        }
      }
    },
    data() {
      return {
        sendTime: 60
      }
    },
    methods: {
      changeSendBtn() {
        if (this.sendTime <= 0) {
          clearInterval(sendBtnTimer)
          this.sendTime = 60
          return false
        }
        this.sendTime--
      },
      getCode() {
        clearInterval(sendBtnTimer)
        this.sendTime = 59
        const data = {
          user_account: JSON.parse(window.localStorage.getItem('link_userInfo')).phone,
          request_type: this.requestType
        }
        sendRequestSms(data).then(response => {
          this.$message({
            message: this.$t('assets.slots.tips.text14'),
            type: 'success',
            duration: 1500
          })
          sendBtnTimer = setInterval(this.changeSendBtn, 1000)
        }).catch(() => {
          this.sendTime = 60 // 错误重置
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .count-down-button {
    display: inline-block;
  }
</style>



